<template>
  <div>
    <div class="star-info" :class="{ 'kol-info': myInfo.type === 1 }">
      <div class="info-item">
        <span>{{ $t('bag.nickname') }}</span>
        <span translate="no">{{ myInfo.nickName }}</span>
      </div>
      <div class="info-item" v-if="!myInfo.type">
        <span>{{ $t('bag.ex') }}</span>
        <span translate="no"
          >{{ upgradeInfo.experience }} / {{ upgradeInfo.needExperience }}</span
        >
      </div>
      <div class="info-item" v-if="!myInfo.type">
        <span>{{ $t('explore.fragment') }}</span>
        <span translate="no"
          >{{ upgradeInfo.fragments }} / {{ upgradeInfo.needFragments }}</span
        >
      </div>
      <div class="info-item">
        <span>NFT{{ $t('bag.contract') }}</span>
        <span translate="no"
          >{{
            $gbUtils.formatAddress($gbUtils.getNftAddress(myInfo), '....', 6)
          }}
          <i
            class="iconfont fuzhi"
            @click="handleCopy($gbUtils.getNftAddress(myInfo))"
          ></i
        ></span>
      </div>
      <div class="info-item">
        <span>{{ $t('bag.number') }}</span>
        <span translate="no"
          >{{ nftName }} #{{ $gbUtils.setNftNum(myInfo.tokenId || 0) }}</span
        >
        <i
          @click="handleShowNft"
          class="iconfont chakan"
          style="padding-left: 5px"
        ></i>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: '',
  props: ['myInfo', 'upgradeInfo', 'nftName'],
  data () {
    return {
    }
  },
  methods: {
    handleCopy (content) {
      const text = document.createElement('textarea')
      text.value = content
      document.body.appendChild(text)
      text.select() // 选择对象
      if (document.execCommand('copy')) {
        document.execCommand('copy')
        this.$toast('copied')
      }
      // 执行浏览器复制命令
      document.body.removeChild(text)
    },
    handleShowNft () {
      this.$parent.nftShow = true
    }
  },
  mounted () {
  }
}
</script>

