          <!-- <img class="cap" src="../../assets/star/cap/魔法帽.png" alt="" /> -->
          <img class="main" :src="starImg" alt="" />
          <!-- <img class="pant" src="../../assets/star/pant/pant3.png" alt="" /> -->
        </div>
      </div>
      <!-- 海星穿戴装备栏 -->
      <div class="star-wear" v-if="false">
