      :upgradeInfo="upgradeInfo"
      @changeNftType="changeNftType"
    ></LevelPop>
    <NftPop
      :info="infoData"
      type="1"
      :nftName="nftName"
      v-if="nftShow"
      @close="handleNftShow"
    ></NftPop>
    <LoadingPop v-if="loadingShow" :needTimer="false"> </LoadingPop>
  </div>
