<script>
import WearPop from './components/WearPop.vue'
import NamePop from './components/NamePop.vue'
import LevelPop from './components/LevelPop.vue'
import StartInfo from './components/StartInfo.vue'
import { getWalletInfo, handleUpgradePage } from '@/services/user'
import NftPop from '../preSale/components/NftPop.vue'
import LoadingPop from '../../components/common/LoadingPop.vue'
import contranctNft from '../../contracts/contractNFT'
import config from '../../config/app.config'

var timerCount = 24 // 轮询总次数
export default {
  name: '',
  data () {
    return {
      timer: null,
      countDownTime: 120,
      loadingShow: false,
      nameShow: false,
      wearShow: false,
      levelShow: false,
      nftShow: false,
      infoData: {
        nickname: '',
        level: 0,
        experience: 0,
        type: 999
      },
      upgradeInfo: {},
      imgArray: {
        starImg: [
          require('../../assets/star/main/main.png'),
          require('../../assets/star/main/main.png'),
          require('../../assets/star/main/main1.png'),
          require('../../assets/star/main/main2.png'),
          require('../../assets/star/main/main3.png'),
          require('../../assets/star/main/main4.png')
        ]
      },
      starImg: '',
      starInfoShow: true,
      nftName: 'StarFish'
    }
  },
  methods: {
    async init () {
      await this.getWalletInfo()
      this.getImg()
      this.getNftName()
      this.getPageInfo()
    },
    handleBack () {
      this.$router.push('/home')
    },
    handleNftShow () {
      this.nftShow = !this.nftShow
    },
    async getNftName () {
      const resp = await contranctNft.getName(this.infoData.type)
      if (!resp.success) return
      this.nftName = resp.result
      this.$nextTick(() => {
        this.starInfoShow = false
        this.starInfoShow = true
      })
    },
    getImg () {
      this.starImg = this.imgArray.starImg[this.infoData.level]
      if (this.infoData.type) {
        this.starImg = this.$gbUtils.getRoleImg(this.infoData.type)
      }
    },
    handleNameShow () {
      this.nameShow = !this.nameShow
    },
    handleWearShow () {
      this.wearShow = !this.wearShow
    },
    handleLevelShow () {
      // 只有普通nft和kol 才可升级
      if (this.infoData.type > 1) return
      this.levelShow = !this.levelShow
    },
    async getWalletInfo () {
      const resp = await getWalletInfo()
      if (!resp.success) return
      this.infoData = resp.data
      this.infoData.nftName = 'StarFish'
      if (resp.data.type) {
        clearInterval(this.timer)
        this.timer = null
        this.loadingShow = false
      }
    },
    async getPageInfo () {
      const resp = await handleUpgradePage()
      if (!resp.success) return
      this.upgradeInfo = resp.data
    },
    async changeNftType () {
      this.loadingShow = true
      const resp = await contranctNft.transferFrom(this.upgradeInfo.rechargeAddress, this.infoData.tokenId, config.SUT)
      this.loadingShow = false
      if (!resp.success) return
      this.loadingShow = true
      this.setTimer()
    },
    setTimer () {
      this.timer = setInterval(() => {
        timerCount = timerCount - 1
        this.getWalletInfo()
        if (timerCount <= 1) {
          clearInterval(this.timer)
          this.timer = null
        }
      }, 5000)
    }
  },
  computed: {
  },
  mounted () {
    this.init()
  },
  components: { WearPop, NamePop, LevelPop, StartInfo, NftPop, LoadingPop }
}
</script>

