<template>
  <VanPopup
    style="
      box-sizing: border-box;
      width: 93%;
      max-width: 400px;
      background-color: #352b27;
      padding: 15px 20px;
    "
    round
    @click-overlay="closePop"
    v-model="show"
  >
    <div class="wrap">
      <div class="icon-wrapper">
        <svg class="icon" aria-hidden="true" @click="closePop">
          <use xlink:href="#guanbixiao"></use>
        </svg>
      </div>
      <div class="name">{{ $t('bag.upgrade') }}</div>
      <div class="content">
        <!-- 团队长不显示 -->
        <div class="des" v-if="!myInfo.type && upgradeInfo.isCan">
          “{{ myInfo.nickName }}”{{ $t('bag.fill') }}
        </div>
        <div class="debris" v-if="!myInfo.type && myInfo.level < 5">
          {{ $t('explore.fragments') }}
          <span translate="no" :class="+upgradeInfo.fragments >= +upgradeInfo.needFragments  ? 'green' : 'red'">
            {{ upgradeInfo.fragments }}</span
          >/{{ upgradeInfo.needFragments }}
        </div>
        <div class="debris" translate="no" v-if="!myInfo.type && myInfo.level < 5">
          {{ $t('explore.experience') }}
          <span translate="no" :class="+upgradeInfo.experience >= +upgradeInfo.needExperience ? 'green' : 'red'">{{
            upgradeInfo.experience
          }}</span
          >/{{ upgradeInfo.needExperience }}
        </div>
        <div class="cvr" translate="no">
          SFO*
          <span :class="+upgradeInfo.balance >= +upgradeInfo.amount ? 'green' : 'red'">{{
            $gbUtils.formatBalance(upgradeInfo.balance)
          }}</span
          >/{{ upgradeInfo.amount }}
        </div>
      </div>
      <div class="button-group">
        <van-button
          class="button-item"
          color="#f8bb5a"
          @click="handleConfirm"
          :disabled="upgradeInfo.isCan === 0"
          >{{ $t('common.confirm') }}</van-button
        >
      </div>
    </div>
  </VanPopup>
</template>

<script>
import { handleUpgrade } from '@/services/user'
export default {
  props: ['myInfo', 'upgradeInfo'],
  name: 'LevelPop',
  data () {
    return {
      show: true
    }
  },
  computed: {
    // isGreen () {
    //   return this.upgradeInfo.experience >= this.upgradeInfo.needExperience && this.upgradeInfo.fragments >= this.upgradeInfo.needFragments && this.upgradeInfo.balance >= this.upgradeInfo.amount
    // }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    async handleConfirm () {
      if (+this.upgradeInfo.balance < +this.upgradeInfo.amount) return
      if (!this.myInfo.type && this.myInfo.level >= 5) return this.changeNftType()
      const resp = await handleUpgrade()
      if (!resp.success) return
      this.$toast('success')
      setTimeout(() => {
        this.$parent.getWalletInfo()
        this.closePop()
      }, 1500)
    },
    async changeNftType () {
      this.closePop()
      this.$emit('changeNftType')
    }
  },
  mounted () {
  }
}
</script>

