<template>
  <VanPopup
    style="
      box-sizing: border-box;
      width: 93%;
      max-width: 400px;
      background-color: #352b27;
      padding: 15px 20px;
    "
    round
    @click-overlay="closePop"
    v-model="show"
  >
    <div class="wrap">
      <div class="icon-wrapper">
        <svg class="icon" aria-hidden="true" @click="closePop">
          <use xlink:href="#guanbixiao"></use>
        </svg>
      </div>
      <div class="name">{{ $t('bag.changeName') }}</div>
      <div class="input-wrap">
        <div class="name">{{ $t('bag.nameNotce') }}</div>
        <van-field
          translate="no"
          class="input"
          :placeholder="$t('bag.inputName')"
          input-align="center"
          maxlength="10"
          v-model="nickname"
        />
      </div>
      <div class="button-group">
        <div class="button-item" @click="handleConfirm">
          {{ $t('common.confirm') }}
        </div>
      </div>
    </div>
  </VanPopup>
</template>

<script>
import { handleNickname } from '@/services/user'
export default {
  name: '',
  data () {
    return {
      show: true,
      nickname: ''
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    async handleConfirm () {
      if (!this.nickname) {
        return this.$toast(this.$t('bag.inputName'))
      }
      const resp = await handleNickname({
        nickname: this.nickname
      })
      if (!resp.success) return
      this.$parent.getWalletInfo()
      this.closePop()
    }
  },
  mounted () {

  }
}
</script>

